

/*Generales*/


html{
  background-color: #333333;
  width: 1920px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*,*::after,*::before{
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
body{
  font-family: Helvetica, sans-serif;
  width: 80%;
  margin: 0 auto;

}
.contenido{
  background-color: white !important;

}
li{
  list-style: none;
}
a{
  text-decoration: none;
  color: black;
}
.clearfix::after{
  content: '';
  display: block;
  clear: both;
}
/*Header*/
.header{
  padding: 30px;
  padding-bottom: 0;
}
.logo{
  text-align: center;
  font-size: 1.8em;
}
.contenedor{
  text-align: center;
}
.contenedor #fecha{
  float: left;
  margin-top:35px;
  display: inline-block;
}
.contenedor #nombre{
  float: right;
  display: inline-block;
  margin-top: 0px;

  color: #218DEF;
}
.contenedor .nav-principal{

  width: 25%;


  display: inline-block;
  margin-right:100px;
}
.contenedor .nav-principal nav {
  text-align: center;


}
.contenedor .nav-principal nav ul{

  margin-right: 40px;
}

.contenedor .nav-principal ul li{

  display: inline-block;
  margin: 10px 10px;
  padding: 0 10px;
  width:20px;
  position: relative;


}
.contenedor .nav-principal ul li ul {
  text-align: center;
  position: absolute;
  top: 20px;
  left: 0;
  visibility: hidden;
  -webkit-filter: opacity(0%);
          filter: opacity(0%);
  background-color: white;
  border-radius: 5px;
  border: 1px solid #e1e1e1;
  width: 200px;
  height: 250px;
  float:left;
  z-index: 3;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;


}
.contenedor .nav-principal ul li:hover ul {
  visibility: visible;
  -webkit-filter: opacity(100%);
          filter: opacity(100%);


}
.contenedor .nav-principal ul li ul li{

  text-align: left;
  width: 100%;

  padding: 0px;
  margin: 10px auto;

}


/**pres**/
.pres{
  height: 500px;
  position: relative;
  z-index: 0;
}
.pres #contenedor{
  height: 500px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.pres #gradiente{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 500px;
  -webkit-filter: opacity(70%);
          filter: opacity(70%);
  background: -webkit-gradient(linear, left top, right top, from(#441178),color-stop(50%, #2989d8),color-stop(51%, #207cca),to(#1177cc));
  background: -webkit-linear-gradient(left, #441178 0%,#2989d8 50%,#207cca 51%,#1177cc 100%);
  background: -o-linear-gradient(left, #441178 0%,#2989d8 50%,#207cca 51%,#1177cc 100%);
  background: linear-gradient(to right, #441178 0%,#2989d8 50%,#207cca 51%,#1177cc 100%);

  z-index: 1;
}

.pres .im{
  background-repeat: no-repeat;
  background-repeat: round;
  height: 500px;
  width:50%;
  float: left;




}
.pres #gradiente #texto-pres{
  color: 	rgb(255,255,255,1);
  float: left;
  width: 33%;
  -webkit-filter: opacity(100%)!important;
          filter: opacity(100%)!important;
  padding:100px;
  font-size: 1.5em;
  font-weight: bold;

}

.pres #autor{
  font-size: 0.8em;
}
/**main**/
.main{

}
.main #Titulares{
    border-bottom: 5px solid black;
}
.main #Titulares h2{
  float: left;
  margin-left: 30px;
}
.main #Titulares p{
  float: right;
  font-weight: bold;
  margin-right: 30px;
}
.main .contenedor{
  padding: 30px;
  padding-top: 0;
}
.main .main-cuerpo{
  width: 70%;
  float:left;

}
.main .aside{
  width: 30%;
  float:right;
}
.main .main-cuerpo .categoria{
  width: 45%;
  float:left;
  text-align: left;
  padding: 20px;
}
.main .main-cuerpo .categoria .imagen{
  width:100%;
  height: 200px;

  margin-bottom: 70px;
}

.main .main-cuerpo .categoria .imagen img{
  width:100%;
  border-radius: 5px;
}
.main .aside #top-noticias{
  padding: 10px;
  text-align: left;
  border: 1px solid black;
}
.main .aside .noticiaTop{
  margin-bottom: 80px;
}
.main .aside .imagen{
  float: left;
  width:25%;
  height: 90px;

  margin-right: 20px;
}
.main .aside .imagen img{
  height: 90px;
  width: 100%;
  border-radius:5px;
}

/**Animaciones**/
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.titulares-enter {
  opacity: 0.01;
}

.titulares-enter.titulares-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.titulares-leave {
  opacity: 1;
}

.titulares-leave.titulares-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}



